* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style: none;

  .menu_item {
    padding-right: 38px;
    border-right: 2px dotted black; }

  .menu_link {
    color: #FFFFFF;
    font-size: 15px;
    font-weight: 300;
    text-decoration: none; } }

.subheader {
  top: 50px;
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  height: 87px;

  .subheader_logo {
    display: block;
    margin-top: 16px;
    max-width: 100%; }

  .subheader_official {
    margin-top: 6px;
    font-weight: 400;
    opacity: 0.5;
    font-size: 13px;
    color: white; }

  .subheader_call {
    margin-top: 19px;
    font-weight: 300;
    font-size: 12px;
    color: #FFFFFF;
    text-align: right; }

  .subheader_phone {
    display: block;
    text-decoration: none;
    font-weight: 400;
    font-size: 22px;
    color: #FFFFFF;
    line-height: 24px;
    text-align: right; }

  .subheader_btn {
    margin-top: 20px;
    width: 172px;
    height: 46px;
    background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    color: #FFFFFF;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  .promo_btn {
    margin-top: 20px;
    width: 172px;
    height: 46px;
    background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    color: #FFFFFF;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); }

  min-height: 800px;
  background: url(../img/header_photo.jpg) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    color: #FFFFFF;
    font-size: 60px;
    font-weight: 900;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  .promo_subheader {
    color: #FFFFFF;
    font-size: 36px;
    font-weight: 700;
    line-height: 48px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  .promo_desc {
    margin-top: 23px;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center; }

  .promo_btn {
    display: block;
    margin: 115px auto 0 auto;
    width: 259px;
    height: 63px;
    font-size: 18px; } }

.work {
  min-height: 850px;
  padding: 20px 0 64px 0;

  .button_block {
    margin: 0 auto;
    width: 115px;
    height: 27px;
    background-color: #000000;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 11px;
    line-height: 25px;
    text-transform: uppercase;
    text-align: center; } }

.app .app_button {
  margin: 0 auto;
  width: 115px;
  height: 27px;
  background-color: #000000;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 11px;
  line-height: 25px;
  text-transform: uppercase;
  text-align: center; }

.work .work_header, .app .app_header {
  margin-top: 32px;
  font-weight: 700;
  font-size: 38px;
  line-height: 48px;
  color: #222222;
  text-align: center;
  text-transform: uppercase; }

.work .reason {
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: center;

  .icon {
    height: 113px;
    width: 113px;
    border-radius: 50%; }

  .text-description {
    width: 370px;
    color: #202020;
    font-weight: 300;
    font-size: 15px;
    line-height: 21px;
    margin-left: 25px;

    > span {
      font-weight: 400;
      font-size: 17px;
      line-height: 21px;
      color: #1EACC7; } } }

.app {
  min-height: 864px;
  padding: 48px 0 80px 0;
  background: url(../img/app_background.png) center (center / cover) no-repeat;

  .app_subheader {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #222222;
    text-align: center; } }
